@mixin fordiv_display{
  position: relative;
  display: inline-block;
  box-sizing: border-box;
}
@mixin mr-4p{
  margin-right: -4px;
}


.section{
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  color: #849FB4;
}


.section{
  margin-top: 150px;
  // line-height: 3em;

}

.divi{
  // margin-top: 4px;

  padding: 10px 0px 10px 10px;
  position:relative;
  box-sizing: border-box;
}


.divi:hover{
  box-sizing: border-box;
  background: #E4EAEF;
  // border: 2px solid #42B1FE;
    .bbutton{
      background: #42B1FE;
      transition: all 0s ease 0s;
    }
}

.ar-down{
  box-sizing: border-box;
  background: #E4EAEF;
  // box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);;

  .bbutton{
    background: #42B1FE;
    transition: all 0s ease 0s;
  }
}


.bbutton{
  // display: none;
  width:35px;
  position: absolute;
  right: 0;
  top:0;
  height:100%;
  border-radius: 0px;
  background: #ffffff;
  border: none;
  border-right: 0px;
  color: #ffffff;
  outline: 0!important;
}

.bbutton::before{
  content: url("../img/aright.svg");

}

.ar-down > .bbutton::before {
  content: url("../img/adown.svg");
}

.table-wrapper{
  box-shadow: .5px .5px 5px 0.1px rgba(211, 211, 211, 0.5);
  margin-bottom: 20px;

  table{
    width:100%;

  }
  tbody{
    width:100%;

  }
  tbody:last-child{

    td:nth-child(odd){
      padding-right: 10px;
      padding-left: 10px;
      padding-top: 20px;
      padding-bottom: 20px;
      width:75%;
      background: #F8FBFD;

    }
    td:nth-child(even){
      width:25%;
      background: rgba(255, 255, 255, 0.52);

    }

  }
  tr{
    width:100%;
  }
  th{
    font-size: 15px;
    color: #42B1FE;
    font-weight: 600;
    padding: 10px;
  }
  th:nth-child(odd){
      width:75%;
      background: #F8FBFD;
      text-align: left;
    }
  th:nth-child(even){
      width:25%;
      background: rgba(255, 255, 255, 0.52);
      text-align: center;
  }
  td{
    padding: 10px;
  }
  td:nth-child(odd){
    width:75%;
    background: #F8FBFD;
    text-align: left;
  }
  td:nth-child(even){
    width:25%;
    background: rgba(255, 255, 255, 0.52);
    text-align: center;
  }
}
